.grid__libros {
}

.grid__libros main {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
    align-content: baseline;
    justify-items: center;
    grid-gap: 20px;
    padding: 25px;
}

.grid__libros header {
    text-align: right;
    padding: 10px;
}